body {
    font-family: 'Neue Montreal';
    font-weight: 500;
    background-color: $main-bg-100;
}

.is-hidden {
    opacity: 0;
    visibility: 0;
    pointer-events: none;
}

*::selection {
    background-color: $accent-color-100;
    color: $light-color-100;
}