.container {
  width: 100%;
  padding: 0 100px;
  margin: 0 auto;
  outline: 1px solid red;

  @media screen and (min-width: 375px) {
    width: 375px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
  }

  @media screen and (min-width: 1440px) {
    width: 1090px;
  }
}